import { Col, Layout, Menu, Row } from 'antd';
import { InfoCircleOutlined } from '@ant-design/icons';

import Icon, { DoubleRightOutlined, MenuFoldOutlined, MenuUnfoldOutlined } from '@ant-design/icons';
import logo from "../../../assets/Logo_v2.png";
import './assets/css/index.css';
import { useEffect, useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { ReactComponent as Test } from './assets/ic1.svg';
import {isMobile} from 'react-device-detect';

const { Sider } = Layout;



const IconTest = () => <Icon component={Test} style={{ fontSize: 18, color: 'white' }} />

const Sidebar = ({ headerHeight }) => {
    const [collapsed, setCollapsed] = useState(false);
    const location = useLocation();

   


    const onCollapse = () => {
        setCollapsed(!collapsed);
    }


    //colapse all submenu when change route
    useEffect(() => {
        // colapse all submenu of backsmith when change route
        const submenu = document.querySelectorAll('.ant-menu-submenu-title');
        submenu.forEach(item => {
            item.classList.remove('ant-menu-submenu-open');
        }
        );

    } , [location]);

    const items = [
        {
            label: (
                <Link to="/">
                    SOUL
                </Link>
            ), key: 'SOUL', icon: <MenuFoldOutlined color='#fff' />
        },
        {
            label: (
                <Link to="sword">
                    SWORD
                </Link>
            ), key: 'SWORD', icon: <DoubleRightOutlined color='#fff' />
        },
        {
            label: (
                <Link to="pvepage">
                    BOSS
                </Link>
            ), key: 'BOSS', icon: <MenuFoldOutlined color='#fff' />
        },
        //battle
        {
            label: (
                <Link to="pvppage">
                    BATTLE
                </Link>
            ), key: 'BATTLE', icon: <DoubleRightOutlined color='#fff' />
        },
        //stacking
        {
            label: (
                <Link to="stakingpage">
                    STAKING
                </Link>
            ), key: 'STACKING', icon: <MenuFoldOutlined color='#fff' />
        },
        {
            label: (
                <Link to="market">
                    MARKET
                </Link>
            ), key: 'MARKET', icon: <DoubleRightOutlined color='#fff' />
        },
        {
            label: (
                <Link to="cardpage">
                    MY STORAGE
                </Link>
            ), key: 'STORAGE', icon: <MenuFoldOutlined color='#fff' />
        },

        {
            label:
                "BLACKSMITH"
            , key: 'BLACKSMITH', icon: <DoubleRightOutlined color='#fff' />,
            children: [
                {
                    label: <Link to="Awake">
                    Awake
                </Link>,
                    key: 'Awake',
                },
                {
                    label: <Link to="Enchant">
                    Enchant
                </Link>,
                    key: 'Enchant',
                },
                {
                    label: <Link to="Salvage">
                    Salvage
                </Link>,
                    key: 'Salvage',
                },
                {
                    label: <Link to="Repair">
                    Repair
                </Link>,
                    key: 'Repair',
                }
            ],
        }
    ];

    const openKeys = () => {
        let openKey = '';
        if (location?.pathname?.includes("pvepage")) openKey = "BOSS";
        if (location?.pathname?.includes("pvppage")) openKey = "BATTLE";
        if (location?.pathname?.includes("staking")) openKey = "STACKING";
        if (location?.pathname?.includes("upgrade")) openKey = "BLACKSMITH";
        if (location?.pathname?.includes("Awake")) openKey = "BLACKSMITH";
        if (location?.pathname?.includes("Enchant")) openKey = "BLACKSMITH";
        if (location?.pathname?.includes("Salvage")) openKey = "BLACKSMITH";
        if (location?.pathname?.includes("Repair")) openKey = "BLACKSMITH";
        if (location?.pathname?.includes("market")) openKey = "MARKET";
        if (location?.pathname?.includes("cardpage")) openKey = "STORAGE";
        //sword
        if (location?.pathname?.includes("sword")) openKey = "SWORD";
        return [openKey];
    }

    const selectedKeys = () => {
        console.log(location?.pathname)
        let activeKey = 'SOUL';
        if (location?.pathname?.includes("pvepage")) activeKey = "BOSS";
        else if (location?.pathname?.includes("pvppage")) activeKey = "BATTLE";
        else if (location?.pathname?.includes("staking")) activeKey = "STACKING";
        else if (location?.pathname?.includes("upgrade")) activeKey = "BLACKSMITH";
        else if (location?.pathname?.includes("Awake")) activeKey = "Awake";
        else if (location?.pathname?.includes("Enchant")) activeKey = "Enchant";
        else if (location?.pathname?.includes("Salvage")) activeKey = "Salvage";
        else if (location?.pathname?.includes("Repair")) activeKey = "Repair";
        else if (location?.pathname?.includes("market")) activeKey = "MARKET";
        else if (location?.pathname?.includes("cardpage")) activeKey = "STORAGE";
        //sword
        else if (location?.pathname?.includes("sword")) activeKey = "SWORD";
        return [activeKey];
    }

    return (
        <Sider width={300} className='sidebar' collapsible collapsed={collapsed} trigger={null} style={{  paddingTop: !collapsed ? "0px" : "0", zIndex:1234567, display: isMobile ? 'none' :''}}>
            <Row align='middle' justify={collapsed ? 'center' : 'space-between'} className='sidebar-header text-center'>
                <Col style={{ display: collapsed ? 'none' : 'block' }}>
                <img src={logo} alt="logo" style={{ width: 250 }} />
                </Col>
                {/* <Col>
                    {!collapsed ?
                        <MenuFoldOutlined onClick={onCollapse} style={{ fontSize: 18 }} /> :
                        <MenuUnfoldOutlined onClick={onCollapse} style={{ fontSize: 18 }} />}
                </Col> */}
            </Row>
            <Menu selectedKeys={selectedKeys()} defaultOpenKeys={openKeys()} mode='inline' style={{  overflow: 'hidden', padding: "25px" }} items={items} />
        </Sider >
    )
}

export default Sidebar;

