import React, { useEffect, useState } from "react";
import { Link, NavLink, useLocation } from "react-router-dom"
import { AppstoreAddOutlined, MenuFoldOutlined, DoubleRightOutlined, CloseOutlined } from '@ant-design/icons';
import './assets/style.css';
export const BottomNav = () => {
    const [menuType, setMenuType] = useState(1);
    const [showAdv, setShowAdv] = useState(false);
    // const items = [
    //     {
    //         label: (
    //             <Link to="/">
    //                 SOUL
    //             </Link>
    //         ), key: 'SOUL', icon: <MenuFoldOutlined color='#fff' />
    //     },
    //     {
    //         label: (
    //             <Link to="pvepage">
    //                 BOSS
    //             </Link>
    //         ), key: 'BOSS', icon: <MenuFoldOutlined color='#fff' />
    //     },
    //     //battle
    //     {
    //         label: (
    //             <Link to="pvppage">
    //                 BATTLE
    //             </Link>
    //         ), key: 'BATTLE', icon: <DoubleRightOutlined color='#fff' />
    //     },
    //     //stacking
    //     {
    //         label: (
    //             <Link to="stakingpage">
    //                 STAKING
    //             </Link>
    //         ), key: 'STACKING', icon: <MenuFoldOutlined color='#fff' />
    //     },
    //     {
    //         label: (
    //             <Link to="market">
    //                 MARKET
    //             </Link>
    //         ), key: 'MARKET', icon: <DoubleRightOutlined color='#fff' />
    //     },
    //     {
    //         label: (
    //             <Link to="cardpage">
    //                 MY STORAGE
    //             </Link>
    //         ), key: 'STORAGE', icon: <MenuFoldOutlined color='#fff' />
    //     },
    //     {
    //         label:
    //             "BLACKSMITH"
    //         , key: 'BLACKSMITH', icon: <DoubleRightOutlined color='#fff' />,
    //         children: [
    //             {
    //                 label: <Link to="option1">
    //                     Option 1
    //                 </Link>,
    //                 key: 'option1',
    //             },
    //             {
    //                 label: <Link to="option2">
    //                     Option 2
    //                 </Link>,
    //                 key: 'option2',
    //             }
    //         ],
    //     }
    // ];
    const changeManu = () => {
        setMenuType(menuType === 1 ? 2 : 1);
    }
    const disbleAdvMenu = () => {
        console.log("disbleAdvMenu");
        setShowAdv(false);

      
    }
    const showAdvanceMenu = () => {
        setShowAdv(!showAdv);

    
    }
    const location = useLocation();

    //highlight active parent menu  when click 1 of its children
    useEffect(() => {
       

        const currentPath = location.pathname;
        let submenu_blacksmith = ["awake", "enchant", "salvage", "repair"]
        // check if location contains any of the submenu_blacksmith set active for blacksmith
        if (submenu_blacksmith.some((item) => currentPath.includes(item))) {
            console.log("blacksmith");
            let element = document.getElementById("blacksmith");
            if(element){
                element.className  = "blacksmith nav-item active";
            }
        }
        else {
            let element = document.getElementById("blacksmith");
            if(element){
                element.className  = "blacksmith nav-item";
            }
        }
    }, [location]);
    //disable advance menu when click outside
    useEffect(() => {
        //if click in element has class name "target-click" then do nothing
        document.addEventListener("click", function (e) {
            if (!e.target?.classList?.contains("target-click") && !e.target?.parentElement?.classList?.contains("target-click")) {
                disbleAdvMenu();
            }
        });
    }, []);


    return (
        <div className="bottomNav">
            <div className="bottom-nav-container">
                {menuType === 1 ?
                    <NavLink to="" className="nav-item">
                        <MenuFoldOutlined color='#fff' />
                        <div className="nav-item-label">SOUL</div>
                    </NavLink > :
                    <NavLink to="market" className="nav-item">
                        <MenuFoldOutlined color='#fff' />
                        <div className="nav-item-label">MARKET</div>
                    </NavLink >
                }
                {menuType === 1 ?
                    <NavLink to="pvepage" className="nav-item">
                        <MenuFoldOutlined color='#fff' />
                        <div className="nav-item-label">BOSS</div>
                    </NavLink > :
                    <NavLink to="cardpage" className="nav-item">
                        <MenuFoldOutlined color='#fff' />
                        <div className="nav-item-label">STORAGE</div>
                    </NavLink >
                }
                <div className="nav-item nav-more-btn-container">
                    <span className="nav-indicator"></span>
                    <div className="nav-item-more-btn" onClick={() => changeManu()}>
                        {menuType === 1 && <AppstoreAddOutlined style={{ color: "#fff", fontSize: 20 }} />}
                        {menuType === 2 && <CloseOutlined style={{ color: "#fff", fontSize: 20 }} />}
                    </div>
                    <span class="more-label">More</span>

                </div>
                {menuType === 1 ? <>
                    <NavLink to="pvppage" className="nav-item">
                        <DoubleRightOutlined color='#fff' />
                        <div className="nav-item-label">BATTLE</div>
                    </NavLink  >
                    <NavLink to="stakingpage" className="nav-item">
                        <MenuFoldOutlined color='#fff' />
                        <div className="nav-item-label">STAKING</div>
                    </NavLink ></>
                    : <>
                        <NavLink to="sword" className="nav-item">
                            <MenuFoldOutlined color='#fff' />
                            <div className="nav-item-label">SWORD</div>
                        </NavLink >
                        <div className="nav-item blacksmith-advmenu target-click" onClick={() => showAdvanceMenu()} >
                            <div  className="blacksmith nav-item target-click" style={{position:"relative"}} id="blacksmith">
                                <MenuFoldOutlined color='#fff' className="target-click"/>
                                <div className="nav-item-label target-click">B.SMITH</div>
                            </div >
                            <div className="blacksmith-hidden" style={{display: showAdv ? "block" : "none",position:"absolute", bottom: "60px"}}>
                                <NavLink to="awake" className="nav-child-item">
                                    <div className="nav-item-label">AWAKE</div>
                                </NavLink >
                                <NavLink to="enchant" className="nav-child-item">
                                    <div className="nav-item-label">ENCHANT</div>
                                </NavLink >
                                <NavLink to="salvage" className="nav-child-item">
                                    <div className="nav-item-label">SALVAGE</div>
                                </NavLink >
                                <NavLink to="repair" className="nav-child-item">
                                    <div className="nav-item-label">REPAIR</div>
                                </NavLink >
                            </div>
                        </div>
                    </>
                }

            </div>
        </div>
    )
}