import React, { useEffect, useState } from "react";

import { useStyles } from "./style";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import metamask from "../../assets/metamask.png";
import "./assets/connectForm.css";
/////
import { getBalance, getBNBBalance, useStoreApi, fetchAccountAllowance } from "../../services";
import { useWeb3 } from "../../services";
import Web3Modal from "web3modal";
import ModalLogin from "../ModalLogin/ModalLogin";
import CloseModal from "../../assets/Icon/CloseModal";
import Metamask from "../../assets/Icon/Metamask";
import WalletConnect from "../../assets/Icon/WalletConnect";
////
import { useWeb3React } from "@web3-react/core";
import { connectors } from "../../services/connectors";


const ModalConnect = ({ title, type, setOpen }) => {
  const [typeWallet, setTypeWallet] = useState("");
  const classes = useStyles();
  const {
    library,
    chainId,
    account,
    activate,
    deactivate,
    active
  } = useWeb3React();
  ////
  const {
    address,
    balance,
    setBalance,
    setAddress,
    setAllowance,
    setEWB,
    EWB,
  } = useStoreApi();
  const web3 = useWeb3();

  const setUserAccount = async (type) => {
    if (window.ethereum) {
      // await window.ethereum.enable();
      setTypeWallet(type);
      localStorage.setItem("WALLET", type);
      if(account) {
        console.log(account);
        setAddress(account);
        setUserBalanceBNB(account);
        setUserBalanceEMB(account);
        let allowance = await fetchAccountAllowance(account);
        if (allowance < 1000000) {
          setAllowance(false);
        } else {
          setAllowance(true);
        }
      };
    } else {
      setOpen(false);
      alert(`Turn on ${type} wallet`);
    }
  };
  ///Lấy số dư
  const setUserBalanceBNB = async (fromAddress) => {
    let balanceBNB = await getBNBBalance(fromAddress);
    setBalance(balanceBNB);
  };
  const providerOptions = {};
  const web3Modal = new Web3Modal({
    network: "mainnet", // optional
    cacheProvider: true, // optional
    providerOptions, // required
  });
  const DisconnectWallet = async () => {
    await web3Modal.clearCachedProvider();
    localStorage.setItem("ADDRESS", null);
    setAddress(null);
    setBalance(null);
    deactivate();
  };
  const setUserBalanceEMB = async (fromAddress) => {
    let balanceEMB = await getBalance(fromAddress);
    setEWB(balanceEMB);
  };

  // if (address !== null) {
  //   return (
      // <ModalLogin
      //   address={address}
      //   balanceBNB={balance}
      //   EWB={EWB}
      //   onLogout={() => DisconnectWallet()}
      // />
  //   )
  // }
  return (
    <>
      {/**Responsive modal connect */}
      {/* <div className={classes.modal} onClick={() => setOpen(false)}>
        <div className={classes.broad}>
          {DataWallet.map((item, index) => (
            <ListButton
              item={item}
              index={index}
              address={address}
              balance={balance}
              EWB={EWB}
              onClick={() => setUserAccount(item.name)}
              type={typeWallet}
            />
          ))}
          <div onClick={() => setOpen(false)}>
            <ExpandMoreIcon />
          </div>
        </div>
      </div> */}
      <div className={classes.modal} onClick={() => setOpen(false)}>
        <div className={classes.broad}>
      {!address ? <div className="connect-form-content">
        <div className="connect-form-body">
          <div className="ant-row ant-row-space-between ant-row-middle modal_header" style={{ rowGap: "0px" }}>
            <div className="ant-col ant-col-lg-10"><p>Connect Wallet</p></div>
            <div className="ant-col ant-col-lg-1">
              <CloseModal width="40px" />
            </div>
          </div>
          <div id="walletRow" className="ant-row ant-row-space-around">

            <button className="ant-col walletCol ant-col-lg-10"
              onClick={() => {
                activate(connectors.injected)
                setUserAccount("MetaMask")
              }}
            >
              <div><Metamask width="40px" /></div>
              <h4>Metamask</h4>
              <p>Connect with Metamask wallet </p>
            </button>

            <button className="ant-col walletCol ant-col-lg-10"
              onClick={() => {
                activate(connectors.walletConnect)
                setUserAccount("WalletConnect")
              }}
            >
              <div><WalletConnect width="40px" /></div>
              <h4>connect wallet</h4>
              <p>Connect with connect wallet </p>
            </button>

          </div>

        </div>

      </div> : <>
      <ModalLogin
        address={address}
        balanceBNB={balance}
        EWB={EWB}
        onLogout={() => DisconnectWallet()}
      />
      </>
      }
</div></div>
    </>
  );
};

export default ModalConnect;


